.ButtonModal {
  width: 20%;
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
}

.WideButton {
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
  width: 90%;
}

h1, h2, h3, h4, h5, h6 {
  padding: 0;
  margin: 0;
}

body h1#Profil {
  text-align: center;
}
body img#UserPic {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
body div#TextBody {
  margin-left: 10px;
}